.products {
  position: relative;

  &__cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding-top: 100px;
    margin-right: auto;
    margin-left: auto;
    max-width: 1110px;
  }

  &__rows {
    margin-top: 70px;
    margin-bottom: 50px;
  }

  &__item {
    margin-bottom: 50px;
  }

  &__button-show-more {
    cursor: pointer;
    color: #fff;
    font-size: 24px;
    font-weight: 700;
    background-color: #0156ff;
    border: none;
    border-radius: 28px;
    padding: 9px 18px;
    margin-bottom: 50px;
    transition: transform 0.4s;

    &:hover {
      transform: scale(1.1);
    }

    &:active {
      background-color: blue;
    }
  }
}

.header {
  max-height: 50px;
  background-color: red;


  &__layout {
    background-color: transparent;
    border: none;
    cursor: pointer;

    &-tile {
      margin-right: 5px;
    }

    &-icon {
      &-tile {
        width: 24px;
        height: 24px;
      }

      &-row {
        width: 25px;
        height: 25px;
      }
    }
  }

  &__avatar {
    display: flex;

    &-title {
      letter-spacing: 1px;
      margin-right: 5px;
    }

    &-icon {
      width: 20px;
    }
  }
}
